

ul.nav-menu{
    list-style: none;
    display: flex;
    justify-content: space-between;
}
ul.nav-menu li{
    margin: 0px 40px; 
    font-size:25px;
}


ul.nav-menu li a {
    text-decoration: none;
    color:#fff;
}
ul.nav-menu li a:active{
    color:#000;
}


/* mobile menu */
ul.mobile-menu{
    list-style: none;
    display: flex;
    justify-content: left;
    align-items: left;
    flex-direction: column;
}
ul.mobile-menu li a{
    text-decoration: none;
    color:#000;
}
ul.mobile-menu li {
  margin: 10px 0px;
  text-align: left;
}
ul.mobile-menu li a:active{
    color:#F24C3D;
}

